.navbar-nav {
  list-style: none;
  padding: 0px;
  margin: 0px;
  display: flex;
  align-items: center;
  height: 100%;
  li {
    margin-right: 30px;
    text-transform: uppercase;
    color: color("white");
    font-size: 16px;
    letter-spacing: 2px;

    a {
      @include link(color("white"), $value: 20);
    }
    &.active,
    a.active {
      font-weight: bold;
      color: color("primary");
    }
  }
  .langs{
    margin-left: auto;
    position: relative;
    span{
      display: inline-block;
      height: 100%;
      padding: 10px;
      border-radius: 10px;
      margin: 0px;
      cursor: pointer;
    }
    .dropdown{
      display: none;
      list-style: none;
      position: absolute;
      right: 0px;
      top: 100%;
      background-color: color('dark');
      padding: 0px;
      margin: 0px;
      border: 2px solid color('white');
      border-radius: 5px;
      border-top-right-radius: 0px;
      border-top-width: 0px;
      z-index: 100;
      overflow: hidden;
      li{
        padding: 10px;
        display: block;
        letter-spacing: 0px;
        font-size: 16px;
        font-weight: bold;
        margin: 0px;
        &:not(:last-child){
          border-bottom: 2px solid white;
        }
        &:hover{
          background-color: color('dark-2');
        }
        a{
          display: block;
          width: 100%;
          height: 100%;
        }
        &.active,
        & a.active{
          background-color: color('violet');
        }
      }
      
    }

    &.show{
      background-color: color('dark');
      .dropdown{
        display: block;
      }

    }

  }
}

footer.navbar {
  padding: 10px 0px;
  position: absolute;
  bottom: 0px;
  left: 0px;
  width: 100%;
  z-index: 99;
  ul.navbar-nav {
    justify-content: center;
    li {
      margin: 0px 20px;
      font-size: 16px;
      font-weight: normal;
      @include md {
        margin: 0px 10px;
        font-size: 14px;
      }
      @include sm {
        margin: 0px 5px;
        font-size: 12px;
      }
    }
  }
}
