html[lang="ar"] {
  .navbar {
    .logo {
      direction: ltr;
    }
  }
  .navbar-nav {
    li {
      margin-right: 0px;
      margin-left: 30px;
      letter-spacing: 0px;
    }
    .langs {
      margin-left: 0px;
      margin-right: auto;
      .dropdown {
        left: 0px;
        right: auto;
        border-top-right-radius: 5px;
        border-top-left-radius: 0px;
      }
    }
  }

  .nav-form {
    input {
      padding: 5px 10px 5px 30px;
    }
    .icon {
      right: auto;
      left: 0px;
      transform: translateY(-50%);
      border-radius: 0px;
      border-top-left-radius: 50px;
      border-bottom-left-radius: 50px;
    }
  }

  .btn-right {
    text-align: left;
  }

  .comment {
    .comment-name {
      margin-right: 0px;
      margin-left: 15px;
    }
  }

  .timer {
    direction: ltr;
  }

  .pagination {
    i {
      transform: rotateY(180deg);
    }
  }

  .grid-col {
    float: right;
  }
}
